import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import tmdbApi from '../../api/tmdbApi'
import apiConfig from '../../api/apiConfig'
import './detail.css'
import CastList from './CastList'
import VideoList from './VideoList'
import Movielist from '../../Components/Movie-list/Movielist'
const Detail = () => {
  const [item, setItem] = useState(null)
  const {category,id} = useParams()
  useEffect(()=>{
    const getDetail=async()=>{
      const response =await tmdbApi.detail(category,id,{params:{}})
      setItem(response)
      window.scrollTo(0,0)
    }
    getDetail()
  },[category,id])
  return (
    <>
  {item && (
    <>
    <div className="banner" style={{backgroundImage: `url(${apiConfig.originalImage(item.backdrop_path || item.poster_path)})`}}>

    </div>
    <div className="movieContentContainer">
      <div className="moviePoster">
        <div className="contentImg" style={{backgroundImage: `url(${apiConfig.originalImage(item.backdrop_path || item.poster_path)})`}}>

        </div>
      </div>
      <div className="content-info">
        <h1 className="contentTitle">
          {item.title||item.name}

        </h1>
        <div className="genres">
            {
              
              item.genres && item.genres.slice(0,5).map((genre,i)=>(
                <span key={i} className='genreItem'>{genre.name}</span>
              ))
            }
        </div>
        <p className="dOverview">
          {item.overview}
        </p>
        <div className="cast">
          <div className="castHeader">
            <h2>Cast</h2>

          </div>
          <CastList id={item.id}/>
        </div>
      </div>
    </div>
      <div className="line"></div>
    <div className="videoContainer">
      <div className="mb3">
        <VideoList id={item.id}/>
      </div>
      <div className="similar">
        <div className="similarHeader">
           <h2>Similar</h2>
        </div>
            <Movielist category={category} type="similar" id={item.id}/>
      </div>
        
    </div>
    
    </>
  )}
    </>
  )
}

export default Detail