import React, { useEffect , useRef, useState} from 'react'
import SwiperCore , {Navigation, Autoplay} from 'swiper'
import {Swiper,SwiperSlide} from 'swiper/react'
import tmdbApi, {category, movieType} from '../../api/tmdbApi'
import apiConfig from '../../api/apiConfig'
import Button, { OutlineButton } from '../button/Button';
import Modal, { ModalContent } from '../modal/Modal';
import './hero.css'
import 'swiper/css'
import {useNavigate} from 'react-router-dom'
const HeroSlide = () => {

  SwiperCore.use([Autoplay])
  const [movieItems,setMovieItems] = useState([])
  
  useEffect(()=>{

    const getMovies = async () => {
      const params = {page: 1}
     try{
          const response = await tmdbApi.getMoviesList(movieType.popular, {params});
          setMovieItems(response.results.slice(1, 4));
          
      }catch{
        console.log('error')
      }
  }
  getMovies();
  },[])


  return (
    <div className='hero-slide'>
      <Swiper
        modules={[Autoplay,Navigation]}
        grabCursor={true}
        spaceBetween={20}
        slidesPerView={1}
        navigation={true}
        autoplay={{delay:3000}}
        
      >
        {
          movieItems.map((item,i)=>(
            
              <SwiperSlide key={i}>
                {({isActive})=>(
                  // <img src={apiConfig.originalImage(item.backdrop_path)}/>
                  <HeroSlideItem item={item} className={`${isActive ? 'active' : ''}`} />
                  // <HeroSlide item={item} className={`${isActive}?'active':''`}/>
                )}
              </SwiperSlide>
            
          ))
        }
      </Swiper>
     
      </div>
  )
}

const HeroSlideItem = props => {

  let hisrory = useNavigate();

  const item = props.item;

  const background = apiConfig.originalImage(item.backdrop_path ? item.backdrop_path : item.poster_path);

  const setModalActive = async () => {
      const modal = document.querySelector(`.modal`);
      console.log(modal)

      const videos = await tmdbApi.getVideos(category.movie, item.id);

      if (videos.results.length > 0) {
          const videSrc = 'https://www.youtube.com/embed/' +videos.results[0].key;
          modal.querySelector('.trailerFrame').setAttribute('src', videSrc)
      } else {
          modal.querySelector('.modal').innerHTML = 'No trailer';
      }

      modal.classList.toggle('active');
  }

  return (
      <div
          className={`hero-slide item ${props.className}`}
          style={{backgroundImage: `url(${background})`}}
      >
          <div className="content container">
              <div className="info">
                <div className="posi">
                  <h2 className="title">{item.title}</h2>
                  <div className="overview">{item.overview}</div>
                  <div className="btns">
                      <button onClick={() => hisrory('/movie/' + item.id)}>
                          Explore
                      </button>
                      {/* <button onClick={setModalActive}>
                          Watch trailer
                      </button> */}
                  </div>
                  </div>
              </div>
              <div className=" poster">
                  <img src={apiConfig.w500Image(item.poster_path)} alt="" />
              </div>
          </div>
      </div>
  )
}

const TrailerModal = props => {
  const item = props.item;

  const iframeRef = useRef(null);

  const onClose = () => iframeRef.current.setAttribute('src', '');

  return (
      <Modal active={false} className={`modal${item.id}`}>
          <ModalContent onClose={onClose}>
              <iframe ref={iframeRef} width="100%" height="500px" title="trailer" className='trailerFrame'></iframe>
          </ModalContent>
      </Modal>
  )
}

export default HeroSlide